// storage   
import storage from "../../firebase/storage.js";
import { ref, uploadBytesResumable, getDownloadURL } from "firebase/storage";

//  id  firestore doc   url 
import updateDriverImg from "./updateDriverImg";

// storage    
// db   url 
export default function addDriverImg(id, img, redirectUrl) {
  try {
    if (img) {
      //  storage     (     )
      const driverImgsRef = ref(storage, `driverImgs/${img.name}`);

      // 'driverImgs' storage  
      const uploadTask = uploadBytesResumable(driverImgsRef, img);

      //  (progress, paused, running)   
      uploadTask.on(
        "state_changed",
        snapshot => {
          const progress =
            (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
          console.log("Upload is " + progress + "% done");
          switch (snapshot.state) {
            case "paused":
              console.log("Upload is paused");
              break;
            case "running":
              console.log("Upload is running");
              break;
          }
        },
        error => {
          console.log(`Can not add Data: ${err}`);
        },
        //   
        //  id  firestore doc   url 
        () => {
          getDownloadURL(uploadTask.snapshot.ref).then(downloadURL => {
            updateDriverImg(id, downloadURL);

            //       
            setTimeout(() => {
              location.href = redirectUrl;
            }, 1000);
          });
        }
      );
    } else {
      setTimeout(() => {
        location.href = redirectUrl;
      }, 1000);
    }
  } catch (err) {
    //   
    // console.log(`Can not add Data: ${err}`);
  }
}
