// db   url 
import db from "../../firebase/db.js";
import { doc, updateDoc } from "firebase/firestore";

// db   url 
export default async function updateDriverImg(id, imgUrl) {
  //        x
  if (imgUrl) {
    try {
      //  id  doc 
      await updateDoc(doc(db, "drivers", `${id}`), {
        imgUrl
      });
    } catch (err) {
      //   
      // console.log(`Can not add Data: ${err}`);
    }
  } else {
    console.log("  .");
  }
}
