const loadPage = document.getElementById("load");

window.addEventListener("load", fadeOutLoadPage);

// DOM, CSSOM     load  2   fadeout
function fadeOutLoadPage() {
  try {
    setTimeout(() => {
      loadPage.style.animation = "fadeOut .6s ease-in-out forwards";
    }, 2000);
  } catch (error) {
    //   
  }
}
